import { Component, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { AddCategoryService } from './../services/AddCategoryService';
import { GetCategoryAndSubcategoryService } from './../services/GetCategoryAndSubcategoryService';
import { GetChemistDataModel } from './../models/GetChemistDataModel';
import { GetAllProductModel } from './../models/GetAllProductModel';
import { GetProductCategoryModel } from './../models/GetProductCategoryModel';
import { GetSubCategoryModel } from './../models/GetSubCategoryModel';
import { GetCategoryByChemistIdModel } from './../models/GetCategoryByChemistIdModel';
import { categoryModel } from './../models/categoryModel';
import { ProductDeleteService } from './../services/ProductDeleteService';
import { GetAllProductService } from './../services/GetAllProductService';
import{deleteProductByproductIdService} from './../services/deleteProductByproductIdService';

import { GetProductSubCategoryModel } from './../models/GetProductSubCategoryModel';
declare var jQuery: any;
@Component({
  selector: 'DeleteProduct',
  providers: [deleteProductByproductIdService,GetAllProductService, ProductDeleteService, GetCategoryAndSubcategoryService],
  styles: [require('./DeleteProduct.style.scss')],
  template: require('./DeleteProduct.template.html'),
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'chemist-page app'
  },
})

export class DeleteProductComponent {
  router: Router;
  public ProductType: any;
  public chemistId: any;
  public GetSubCategoryModelArray: GetSubCategoryModel[] = [];
  public AddCategoryServiceArray: AddCategoryService[] = [];
  public GetCategoryByChemistIdModelArray: GetCategoryByChemistIdModel[] = [];
  public GetProductSubCategoryModelArray: GetProductSubCategoryModel[] = [];
  public GetProductCategoryModelArray: GetProductCategoryModel[] = [];
  public GetChemistProductDataModelArray: GetChemistDataModel[] = [];
  public GetAllProductCategory: any[] = [];
  public GetAll_ProductModelArraySingle: GetAllProductModel[] = [];
  public CategoryId: any;
  public UserType:any;


  constructor( public _DeleteProductByproductIdService:deleteProductByproductIdService,public _GetProductService: GetAllProductService, public _productDeleteService: ProductDeleteService, public _getChemistDataService: GetCategoryAndSubcategoryService, router: Router) {
    this.UserType=localStorage.getItem("UserType");
    if(this.UserType==null)
    {
      this.router.navigate(["/app/login"]);
    
    }
    else
    {
    this.router = router;
    this.chemistId = localStorage.getItem("GetChemistId");
    this.ProductType = 100;

    this._getChemistDataService.GetChemistService().subscribe(
      response => {
        for (var i = 0; i < response.data.length; i++) {

          this.GetChemistProductDataModelArray.push(response.data[i]);

        }
        jQuery("#snackbar").html("Show Pharmacacy Name  Successfully");
        this.myFunction();
        console.log('Chemist array', this.GetChemistProductDataModelArray);

      });
    }


  }
  getProductName(PharmacyId) {
    console.log("Inside");

    this.chemistId = PharmacyId;
    console.log(this.chemistId);
    this._GetProductService.GetPharmacyNameService(this.chemistId, this.ProductType).subscribe(
      response => {
        console.log(response.data);

        this.GetAll_ProductModelArraySingle = response.data;
        console.log('chemistName', this.GetAll_ProductModelArraySingle);

      });
  }
  getProductId(ProductId) {
    console.log('product Id', ProductId);

    this._DeleteProductByproductIdService.DeleteProductIdService(ProductId).subscribe(
      response => {
        console.log(response);
      });
      jQuery("#snackbar").html("Delete Product  Successfully");
      this.myFunction();
      var index = this.GetAll_ProductModelArraySingle.findIndex(x => x.Id == ProductId);
      this.GetAll_ProductModelArraySingle.splice(index, 1);
  }
 


  myFunction() {
    // Get the snackbar DIV
    var x = document.getElementById("snackbar")

    // Add the "show" class to DIV
    x.className = "show";

    // After 3 seconds, remove the show class from DIV
    setTimeout(function () { x.className = x.className.replace("show", ""); }, 3000);
  }





}





